import React from 'react'
import {useState,useEffect} from 'react'
import {UplineIcon} from '../common'
import Skeleton from '../skeleton'
import Cookies from 'js-cookie'
import Web3 from 'web3'
import defaultVars from '../../libs/config'
import contractAbi from '../../libs/abi'
const web3 = new Web3(defaultVars.provider);
const contract = new web3.eth.Contract(contractAbi,defaultVars.contractAddress);

const Uplines = () => {
    const [uplines,setUplines] = useState(null);
    useEffect(()=>{
        const address = Cookies.get('user');
        const a = async () => {
            let up = [];
            const user = await contract.methods.us(address).call();
            if ( Number(user.b.toString()) === 0){
                return setUplines([]);
            }
            const up1Address = await contract.methods.uL(Number(user.b.toString())).call();
            up.push({id: user.b.toString(),address: up1Address});
            const user1 = await contract.methods.us(up1Address).call();
            if (Number(user1.b.toString()) === 0){
                return setUplines(up);
            }
            const up2Address = await contract.methods.uL(Number(user1.b.toString())).call();
            up.push({id: user1.b.toString(),address: up2Address});
            const user2 = await contract.methods.us(up2Address).call();
            if (Number(user2.b.toString()) === 0){
                return setUplines(up);
            }
            const up3Address = await contract.methods.uL(Number(user2.b.toString())).call();
            up.push({id: user2.b.toString(),address: up3Address});
            const user3 = await contract.methods.us(up3Address).call();
            if (Number(user3.b.toString()) === 0){
                return setUplines(up);
            }
            const up4Address = await contract.methods.uL(Number(user3.b.toString())).call();
            up.push({id: user3.b.toString(),address: up4Address});
            const user4 = await contract.methods.us(up4Address).call();
            if (Number(user4.b.toString()) === 0){
                return setUplines(up);
            }
            const up5Address = await contract.methods.uL(Number(user4.b.toString())).call();
            up.push({id: user4.b.toString(),address: up5Address})
            return setUplines(up);
        }
        a();
    },[null])
    let ups = ['1st','2nd','3rd','4th','5th'];
    return (
        <div className="px-2 py-2 lg:px-8 lg:py-8 shadow bg-white">
            <div className="mb-8 font-sans font-bold text-16 text-lights-orangebold uppercase text-center">Uplines</div>
            <div className="">
                {ups.map((val,key)=>{
                    return (
                        <div className="text-lights-bluebold flex items-center mb-4 overflow-x-auto" key={val}>
                            {uplines === null ? <Skeleton width={40} height={40} circle/> : <UplineIcon large/>}
                            <div className="ml-4">
                                {uplines === null ? <div className="mb-2"><Skeleton width={180} height={18} /></div> :<div className="font-bold text-16 text-lights-bluebold mb-2">
                                    {val} line upline
                                </div>}
                                {uplines === null ? <div className=""><Skeleton width={280} height={14}/></div> : <div className="text-14 text-lights-gray font-sans font-semibold">
                                    <span className="italic">Address: </span>
                                    <span className="text-lights-orangebold">{uplines[key] && uplines[key].address ? uplines[key].address : '--'}</span>
                                </div>}
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}
export default Uplines